<template>
  <div id="posts">
    <ul>
      <li v-for="post in posts">
        {{post.title}} <br/>
        {{post.content}}
      </li>
    </ul>
  </div>
</template>

<script>
var getPosts = require('../data/api.js').getPosts;
var posts;

export default {
  name: 'posts',
  created: function () {
    var self = this;
    getPosts(function (data) {
    console.log(data[0].title);
    self.$set('posts',data);
    });
  },
  data () {
    return { 'posts':[] };
  }
}
</script>

<style>
#posts {
  height:450px;
  width: 490px;
  float: left;
}
</style>



